*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.quizgame{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2cm;
}

.quiz-game{
    margin-bottom: 1cm;
    background: linear-gradient(red, blue, yellow);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 1cm;
}

.quiz-ques{
    box-shadow: 0.5px 0.5px 10px rgba(0, 0, 0, 0.181);
    max-width: 18cm;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    margin: 10px;
    padding: 10px;
}

.ques-no{
    margin-right: 5px;
}

.ques{
    text-align: center;
}

.ans-list{
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.ans{
    list-style: none;
    font-size: 20px;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.181);
    padding: 10px 100px;
    text-transform: capitalize;
}

.ans:hover{
    background-color: rgba(0, 0, 255, 0.477);
}

.next-ques{
    padding: 8px 40px;
    font-size: 0.5cm;
    background-color: transparent;
    border: 1px solid black;
    cursor: pointer;
    border-radius: 5px;
}

.next-ques:hover{
    background-color: rgba(55, 55, 247, 0.708);
}

.score-board{
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
}

.try-again{
    padding: 10px 25px;
    font-size: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    color: black;
    background-color: white;
    border: 1px solid black;
    border-radius: 10px;
    margin-top: 10px;
}

.try-again:hover{
    background-color: rgba(0, 0, 0, 0.888);
    color: white;
}
